import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from '@angular/forms';
import { UserService } from '../../../shared/user.service';
import { MeterService } from '../../../shared/meter.service';
import { Chart } from "chart.js";

export interface Month {
  month: string;
  m: string;
}

export interface Year {
  year: string;
  y: string;
}

@Component({
  selector: 'app-bill-all',
  templateUrl: './bill-all.component.html',
  styleUrls: ['./bill-all.component.css']
})
export class BillAllComponent implements OnInit {
  show: boolean = false;
  selectedMonth: string;
  selectedYear: string = '2018';
  monthForm = new FormControl('', [Validators.required]);
  yearForm = new FormControl('', [Validators.required]);
  months: Month[] = [
    { month: 'January', m: 'มกราคม' }, { month: 'February', m: 'กุมภาพันธ์' }, { month: 'March', m: 'มีนาคม' }, { month: 'April', m: 'เมษายน' },
    { month: 'May', m: 'พฤษภาคม' }, { month: 'June', m: 'มิถุนายน' }, { month: 'July', m: 'กรกฎาคม' }, { month: 'August', m: 'สิงหาคม' },
    { month: 'September', m: 'กันยายน' }, { month: 'October', m: 'ตุลาคม' }, { month: 'November', m: 'พฤศจิกายน' }, { month: 'December', m: 'ธันวาคม' },
  ];

  years: Year[] = [
    { year: '2018', y: '2561' }
  ];
  constructor(private userService: UserService, private meterService: MeterService) { }
  userDetails
  ngOnInit() {
    this.userService.getUserProfile().subscribe(
      res => {
        this.userDetails = res['user'];
        this.getmeter(this.userDetails.room)
      },
      err => {
        console.log(err);
      }
    );

    var month = new Array();
    month[0] = "January";
    month[1] = "February";
    month[2] = "March";
    month[3] = "April";
    month[4] = "May";
    month[5] = "June";
    month[6] = "July";
    month[7] = "August";
    month[8] = "September";
    month[9] = "October";
    month[10] = "November";
    month[11] = "December";
    const time = new Date().getMonth()
    this.selectedMonth = month[time];
  }

  Maddr;
  getmeter(room) {
    this.userService.MacFromRoom(room).subscribe(
      res => {
        this.Maddr = res['Maddr'];
      },
      err => {
        console.log(err);
      }
    );
  }
  LineChart = [];

  bill;
  userDetailsBath;
  submit() {

    this.meterService.showBillUserReport(this.Maddr, this.selectedMonth, this.selectedYear).subscribe(
      res => {
        this.bill = res
        console.log(this.bill);
        this.conToTH(res)
      },
      err => {
        console.log(err);
      }
    );
    this.meterService.showBathPerNum().subscribe(
      res => {
        this.userDetailsBath = res['bathPerNum'];
        console.log(this.userDetailsBath);
      },
      err => {
        console.log(err);
      }

    );
    this.meterService.showStatistic(this.selectedMonth, this.selectedYear, this.userDetails.room).subscribe(data => {
      if (!data) {
        return;
      }
      console.log(data);

      let ActiveEnergy = Object.keys(data).map(function (personNamedIndex) {
        let data1 = data[personNamedIndex];
        return data1.ActiveEnergy;
      });
      let date = Object.keys(data).map(function (personNamedIndex) {
        let data1 = data[personNamedIndex];
        return data1.date;
      });

      this.LineChart = new Chart("ActiveEnergy", {
        type: 'line',
        data: {
          labels: date,//[1500, 1600, 1700, 1750, 1800, 1850, 1900, 1950, 1999, 2050],
          datasets: [{
            data: ActiveEnergy,//[40, 20, 10, 16, 24, 38, 74, 167, 508, 784],
            borderColor: "#e8c3b9",
            fill: false
          }
          ]
        },
        options: {
          title: {
            display: true,
            text: 'ค่าพลังงานไฟฟ้าเดือน ' + this.conMonthTh(this.selectedMonth) +" "+ this.conYearTh(this.selectedYear)
          },
          scales: {
            xAxes: [{
              type: 'time',
              time: {
                displayFormats: {
                  quarter: 'MMM YYYY'
                }
              }
            }]
          },
          tooltips: {
            callbacks: {
              label: function (tooltipItem) {
                return tooltipItem.yLabel;
              }
            }
          },
          legend: {
            display: false
          }
        }
      });
    })



  }
  selectedMonthTH;
  selectedYearTH;
  day; daynum; Month; year; time;
  eday; edaynum; eMonth; eyear; etime;
  conToTH(data) {
    this.selectedMonthTH = this.conMonthTh(this.selectedMonth);
    this.selectedYearTH = this.conYearTh(this.selectedYear);

    var start = data.startFullTime.date
    console.log(start.split(",", 5))
    if (start == 'ไม่มีการบันทึกค่า') {
      this.time = start
    } else {
      var time = start.split(",", 5)
      this.day = this.conDayTH(time[0])
      this.daynum = time[1]
      this.Month = this.conMonthTh(time[2].split(" ", 2)[1]);
      this.year = this.conYearTh(time[3].split(" ", 2)[1]);
      this.time = time[4]
    }
    var end = data.lastFullTime.date
    console.log(end.split(",", 5))
    if (end == 'ไม่มีการบันทึกค่า') {
      this.time = end
    } else {
      var time = end.split(",", 5)
      this.eday = this.conDayTH(time[0])
      this.edaynum = time[1]
      this.eMonth = this.conMonthTh(time[2].split(" ", 2)[1]);
      this.eyear = this.conYearTh(time[3].split(" ", 2)[1]);
      this.etime = time[4]
    }
  }
  conYearTh(selectedyear) {
    return parseInt(selectedyear) + 543
  }
  conMonthTh(selectedMonth) {
    var selectedMonthTH
    if (selectedMonth == 'January') {
      selectedMonthTH = 'มกราคม'
    } else if (selectedMonth == 'February') {
      selectedMonthTH = 'กุมภาพันธ์';
    } else if (selectedMonth == 'March') {
      selectedMonthTH = 'มีนาคม';
    } else if (selectedMonth == 'April') {
      selectedMonthTH = 'เมษายน';
    } else if (selectedMonth == 'May') {
      selectedMonthTH = 'พฤษภาคม';
    } else if (selectedMonth == 'June') {
      selectedMonthTH = 'มิถุนายน';
    } else if (selectedMonth == 'July') {
      selectedMonthTH = 'กรกฎาคม';
    } else if (selectedMonth == 'August') {
      selectedMonthTH = 'สิงหาคม';
    } else if (selectedMonth == 'September') {
      selectedMonthTH = 'กันยายน';
    } else if (selectedMonth == 'October') {
      selectedMonthTH = 'ตุลาคม';
    } else if (selectedMonth == 'November') {
      selectedMonthTH = 'พฤศจิกายน';
    } else if (selectedMonth == 'December') {
      selectedMonthTH = 'ธันวาคม';
    }
    return selectedMonthTH
  }
  conDayTH(data) {
    var dayTH
    if (data == 'Sunday') {
      dayTH = 'อาทิตย์'
    } else if (data == 'Monday') {
      dayTH = 'จันทร์'
    } else if (data == 'Tuesday') {
      dayTH = 'อังคาร'
    } else if (data == 'Wednesday') {
      dayTH = 'พุธ'
    } else if (data == 'Thursday') {
      dayTH = 'พฤหัสบดี'
    } else if (data == 'Friday') {
      dayTH = 'ศุกร์'
    } else if (data == 'Saturday') {
      dayTH = 'เสาร์'
    } else {
      dayTH = 'ไม่มีการบันทึกค่า'
    }
    return dayTH
  }
}